@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins',sans-serif; }

body {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: #07d1ff; }



.navigation {
  position: relative;
  top: 20px;
  left: 10px;
  width: 220px;
  height: 70px;
  background: #fff;
  border-radius: 35px;
  box-shadow: 0 15px 25px rgba(0, 0, 0, 0.1); }

.navigation ul {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  display: flex; }

.navigation ul li {
  position: relative;
  list-style: none;
  width: 70px;
  height: 70px;
  z-index: 1; }

.navigation ul li a {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  text-align: center;
  color: #333;
  font-weight: 500; }

.navigation ul li a .icon {
  position: relative;
  display: block;
  line-height: 75px;
  text-align: center;
  transition: 0.5s; }

.navigation ul li:hover a .icon,
.navigation ul li.active a .icon {
  color: #fff; }

.navigation ul li a .icon .fas {
  font-size: 24px; }

.navigation ul li a .title {
  position: absolute;
  top: -70px;
  left: 50%;
  transform: translate(-50%,15%);
  background: #fff;
  width: auto;
  padding: 5px 10px;
  border-radius: 6px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  transition: 0.5s;
  opacity: 0;
  visibility: hidden; }

.navigation ul li:hover a .title {
  opacity: 1;
  visibility: visible;
  transform: translate(-50%,50%); }

.navigation ul li a .title::before {
  content: '';
  position: absolute;
  width: 12px;
  height: 12px;
  background: #fff;
  bottom: -8px;
  left: 48%;
  transform: rotate(45deg) translateX(-50%);
  border-radius: 2px; }

.navigation ul .indicator {
  position: absolute;
  left: 0;
  width: 70px;
  height: 70px;
  transition: 0.5s; }

.navigation ul .indicator::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  background: #333;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  transition: 0.5s; }


.navigation ul li:nth-child(1).active ~ .indicator {
  transform: translateX(calc(70px * 0)); }

.navigation ul li:nth-child(2).active ~ .indicator {
  transform: translateX(calc(70px * 1)); }

.navigation ul li:nth-child(3).active ~ .indicator {
  transform: translateX(calc(70px * 2)); }


.navigation ul li:nth-child(1).active ~ .indicator::before {
  background: #3c40c6; }

.navigation ul li:nth-child(2).active ~ .indicator::before {
  background: #05c46b; }

.navigation ul li:nth-child(3).active ~ .indicator::before {
  background: #0fbcf9; }






.sidebar {
  position: fixed;
  left: -250px;
  width: 250px;
  height: 100%;
  background: #042331;
  transition: all .5s ease;

  header {
    font-size: 22px;
    color: white;
    line-height: 70px;
    text-align: center;
    background: #063146;
    user-select: none; } }


#check {
  display: none; }

label {
  #btn, #cancel {
    position: absolute;
    background: #042331;
    border-radius: 3px;
    cursor: pointer; }

  #btn {
    left: 40px;
    top: 25px;
    font-size: 35px;
    color: white;
    padding: 6px 12px;
    transition: all .5s; }

  #cancel {
    z-index: 1111;
    left: -195px;
    top: 17px;
    font-size: 30px;
    color: #0a5275;
    padding: 4px 9px;
    transition: all .5s ease; } }

#check:checked ~ {
  .sidebar {
    left: 0; }

  label {
    #btn {
      left: 250px;
      opacity: 0;
      pointer-events: none; }

    #cancel {
      left: 195px; } }

  section {
    margin-left: 250px; } }









.container {
  position: relative;
  max-width: 800px;
  padding: 10px;
  margin: 50px auto; }

.addTask {
  width: 100%;
  display: flex;
  padding: 10px 0;

  > input[type="text"] {
    width: 90%;
    color: #333;
    border: none;
    border-bottom: 2px solid #666;
    background: none;
    padding: 10px;
    font-size: 22px;
    outline: none; }

  button {
    margin: 0 10px;
    background: #222;
    color: #fff;
    font-size: 18px;
    text-transform: uppercase;
    padding: 10px;
    border: none;
    outline: none;
    border-radius: 5px;
    cursor: pointer; } }

.notCompleted, .Completed {
  margin: 5px 0;
  padding: 20px; }

h3 {
  font-size: 28px;
  text-transform: uppercase;
  text-align: center;
  color: #fff; }

ol {
  list-style: none;
  counter-reset: my-counter;

  li {
    width: 100%;
    padding: 15px;
    margin: 5px 0;
    background: #e96969bb;
    font-size: 18px;
    counter-increment: my-counter;
    border-radius: 10px;
    background: #bdc3c7; }

  &.Completed li {
    background: #00ff62; } }

li button {
  float: right;
  margin-right: 0px;
  padding: 5px;
  font-size: 20px;
  line-height: 20px;
  background: none;
  border: none;
  outline: none;
  cursor: pointer; }


li #Status {
  font-size: 10px;
  position: absolute;
  left: 5px; } //mybe 35px



h1 {
  position: relative;
  color: #fff;
  font-size: 10em;
  line-height: 1.1em;
  outline: none;
  text-align: center;
  user-select: none;
  transform: rotate(-10deg) skewX(20deg);
  text-shadow: -1px 1px #cacaca, -2px 2px #cacaca, -3px 3px #cacaca, -4px 4px #cacaca, -5px 5px #cacaca, -6px 6px #cacaca, -7px 7px #cacaca, -8px 8px #cacaca, -9px 9px #cacaca, -10px 10px #cacaca, -11px 11px #cacaca, -12px 12px #cacaca, -20px 20px rgba(0, 0, 0, 0.2); }




#UsernameButtonUl {
  position: absolute;
  top: 80%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  margin: 0;
  padding: 0; }

#UsernameButtonUl #UsernameButtonLi {
  list-style: none;
  margin: 0 5px; }

#UsernameButtonUl #UsernameButtonLi #UsernameButtonSpan {
  font-family: 'Poppins',sans-serif;
  padding-left: 20px;
  margin: 0;
  position: absolute;
  top: 30px;
  color: #333;
  letter-spacing: 4px;
  transition: 0.5s; }

#UsernameButtonUl #UsernameButtonLi a {
  text-decoration: none;
  display: absolute;
  display: block;
  width: 210px;
  height: 80px;
  background: #fff;
  text-align: left;
  padding-left: 20px;
  transform: rotate(-10deg) skewX(20deg) translate(0,0);
  transition: 0.5s;
  box-shadow: -20px 20px 10px rgba(0, 0, 0, 0.5); }

#UsernameButtonUl #UsernameButtonLi a:before {
  content: '';
  position: absolute;
  top: 10px;
  left: -20px;
  height: 100%;
  width: 20px;
  background: #b1b1b1;
  transform: 0.5s;
  transform: rotate(0deg) skewY(-45deg); }

#UsernameButtonUl #UsernameButtonLi a:after {
  content: '';
  position: absolute;
  bottom: -20px;
  left: -10px;
  height: 20px;
  width: 100%;
  background: #b1b1b1;
  transform: 0.5s;
  transform: rotate(0deg) skewX(-45deg); }

#UsernameButtonUl #UsernameButtonLi a:hover {
  transform: rotate(-10deg) skew(20deg) translate(20px,-15px);
  box-shadow: -50px 50px 50px rgba(0, 0, 0, 0.5); }


#UsernameButtonUl #UsernameButtonLi:hover a {
  background: #042331; }


#UsernameButtonUl #UsernameButtonLi:hover a:before {
  background: #042331; }


#UsernameButtonUl #UsernameButtonLi:hover a:after {
  background: #042331; }



#UsernameButtonUl #UsernameButtonLi:hover #UsernameButtonSpan {
  color: #fff; }

